import React from "react";
import {
  List,
  ListItem,
  ListItemText,
  IconButton,
  ListItemSecondaryAction,
  Divider
} from "@material-ui/core";
import OpenInNewIcon from "@material-ui/icons/OpenInNew";

export default props => {
  let list = [];

  props.sites.map((site, i) =>
    list.push(
      <React.Fragment key={i}>
        <ListItem
          key={"s" + i}
          button
          alignItems="flex-start"
          onClick={event => window.open(site.url, "_blank")}
        >
          <ListItemText primary={site.name} />
          <ListItemSecondaryAction>
            <IconButton
              edge="end"
              onClick={event => window.open(site.url, "_blank")}
            >
              <OpenInNewIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
        <Divider />
      </React.Fragment>
    )
  );

  return (
    <div
      className={`animated ${props.isMobile ? "fadeInLeft" : "fadeIn"} faster`}
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
      }}
    >
      <List style={{ width: 300 }}>{list}</List>
    </div>
  );
};
