import React from "react";
import { Grid } from "@material-ui/core";
import { TCard } from "../Components";

export default props => {
  let list = [];

  props.tools.map((tool, i) =>
    list.push(
      <Grid item key={i}>
        <TCard name={tool.name} url={tool.url} description={tool.description} />
      </Grid>
    )
  );

  return (
    <Grid
      container
      justify="center"
      spacing={2}
      className={`animated ${props.isMobile ? "fadeInLeft" : "fadeIn"} faster`}
    >
      {list}
    </Grid>
  );
};
