import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { BottomNavigation, BottomNavigationAction } from "@material-ui/core";
import SubscriptionsOutlinedIcon from "@material-ui/icons/SubscriptionsOutlined";
import FeaturedPlayListOutlinedIcon from "@material-ui/icons/FeaturedPlayListOutlined";
import WebOutlinedIcon from "@material-ui/icons/WebOutlined";
import AppsOutlinedIcon from "@material-ui/icons/AppsOutlined";

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    width: "100%",
    zIndex: 10,
    bottom: 0,
    position: "fixed",
    boxShadow:
      "0 -4px 5px 0 rgba(0,0,0,0.18), 0 -1px 10px 0 rgba(0,0,0,0.08), 0 -2px 4px -1px rgba(0,0,0,0.16)"
  }
});

export default props => {
  const classes = useStyles();

  return (
    <BottomNavigation
      value={props.tabIndex}
      onChange={props.handleTabChange}
      className={`animated fadeInUp faster ${classes.root}`}
      position="sticky"
    >
      <BottomNavigationAction
        label="Channels"
        value={0}
        icon={<SubscriptionsOutlinedIcon />}
      />
      <BottomNavigationAction
        label="Playlists"
        value={1}
        icon={<FeaturedPlayListOutlinedIcon />}
      />
      <BottomNavigationAction
        label="Programs"
        value={2}
        icon={<AppsOutlinedIcon />}
      />
      <BottomNavigationAction
        label="Other"
        value={4}
        icon={<WebOutlinedIcon />}
      />
    </BottomNavigation>
  );

  /*
  return (
    <Paper square style={{
        flexGrow: 1,
    zIndex: 10,
    bottom: 0,
    width: "100%",
    position: "fixed",
    maxWidth: 500
      }}>
      <Tabs
        value={props.tabIndex}
        onChange={props.handleTabChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab icon={<SubscriptionsOutlinedIcon />} />
        <Tab icon={<FeaturedPlayListOutlinedIcon />} />
        <Tab icon={<AppsOutlinedIcon />} />
        <Tab icon={<WebOutlinedIcon />} />
        <Tab icon={<ExtensionOutlinedIcon />} />
      </Tabs>
    </Paper>
  );*/
};
