import React from "react";
import {
  Channels,
  MyCodeExtensions,
  Other,
  Playlists,
  Sites,
  Tools
} from "../Pages";
import JsonData from "../Assets/data.json";

export default class Body extends React.Component {
  UNSAFE_componentWillMount() {
    JsonData.channels.sort((a, b) =>
      a.description.localeCompare(b.description)
    );
  }

  figure = () => {
    if (this.props.tabIndex === 0)
      return (
        <Channels channels={JsonData.channels} isMobile={this.props.isMobile} />
      );
    else if (this.props.tabIndex === 1)
      return (
        <Playlists
          playlists={JsonData.playlists}
          isMobile={this.props.isMobile}
        />
      );
    else if (this.props.tabIndex === 2)
      return <Tools tools={JsonData.tools} isMobile={this.props.isMobile} />;
    else if (this.props.tabIndex === 3 && !this.props.isMobile)
      return <Sites sites={JsonData.sites} isMobile={this.props.isMobile} />;
    else if (this.props.tabIndex === 3)
      return (
        <Other
          myCodeExtensions={JsonData.myCodeExtensions}
          sites={JsonData.sites}
          isMobile={this.props.isMobile}
        />
      );
    else if (this.props.tabIndex === 4 && !this.props.isMobile)
      return (
        <MyCodeExtensions
          myCodeExtensions={JsonData.myCodeExtensions}
          isMobile={this.props.isMobile}
        />
      );
    else if (this.props.tabIndex === 4)
      return (
        <Other
          myCodeExtensions={JsonData.myCodeExtensions}
          sites={JsonData.sites}
          isMobile={this.props.isMobile}
        />
      );
    else return <p>{this.props.tabIndex}</p>;
  };

  render() {
    return <div style={{ marginTop: "1em" }}>{this.figure()}</div>;
  }
}
