import React from "react";
import {
  Card,
  CardActions,
  CardContent,
  Button,
  Typography
} from "@material-ui/core";

export default props => {
  return (
    <Card style={{ width: 300 }}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {props.name}
        </Typography>
        <Typography variant="body2" component="p">
          {props.description}
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          size="small"
          onClick={event => window.open(props.url, "_blank")}
        >
          Learn More
        </Button>
      </CardActions>
    </Card>
  );
};
