import React from "react";
import { Typography } from "@material-ui/core";
import Channels from "./Channels";
import MyCodeExtensions from "./MyCodeExtensions";
import Playlists from "./Playlists";
import Sites from "./Sites";
import Tools from "./Tools";

const Other = props => (
  <>
    <Typography
      className={`animated ${props.isMobile ? "fadeInLeft" : "fadeIn"} faster`}
      color="secondary"
      variant="h5"
      align="center"
      gutterBottom
    >
      Useful Sites
    </Typography>
    <Sites sites={props.sites} isMobile={props.isMobile} />
    <Typography
      className={`animated ${props.isMobile ? "fadeInLeft" : "fadeIn"} faster`}
      color="secondary"
      style={{ marginTop: "1em" }}
      variant="h5"
      align="center"
      gutterBottom
    >
      My VS Code Extensions
    </Typography>
    <MyCodeExtensions
      myCodeExtensions={props.myCodeExtensions}
      isMobile={props.isMobile}
    />
  </>
);

export { Channels, MyCodeExtensions, Other, Playlists, Sites, Tools };
