import React from "react";
import { Grid } from "@material-ui/core";
import { CCard } from "../Components";

export default props => {
  let list = [];

  props.channels.map((channel, i) =>
    list.push(
      <Grid item key={i}>
        <CCard
          name={channel.name}
          channelUrl={channel.channelUrl}
          imgUrl={channel.imgUrl}
          description={channel.description}
          longDescription={channel.longDescription}
        />
      </Grid>
    )
  );

  return (
    <Grid
      container
      justify="center"
      spacing={2}
      className={`animated ${props.isMobile ? "fadeInLeft" : "fadeIn"} faster`}
    >
      {list}
    </Grid>
  );
};
