import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  ButtonBase
} from "@material-ui/core";

const useStyles = makeStyles({
  card: {
    width: 305,
    height: 295
  },
  media: {
    height: 125
  },
  cardAction: {
    display: "block",
    textAlign: "initial"
  }
});

export default props => {
  const classes = useStyles();

  function openNewTab() {
    window.open(props.url, "_blank");
  }

  return (
    <ButtonBase className={classes.cardAction} onClick={event => openNewTab()}>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image={props.imgUrl}
          title={props.name}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {props.name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {props.description}
          </Typography>
        </CardContent>
      </Card>
    </ButtonBase>
  );
};
